import React, { useRef } from 'react';
import domtoimage from 'dom-to-image';
import { createPortal } from 'react-dom'

import Box from 'components/Box';
import Flex from 'components/Flex';
import Circle from 'components/Circle';
import Download from 'components/Download';
import Text from 'components/Text';
import Button from 'components/Button';
import StyledImage from 'components/Image';
import Title from 'components/Title';
import Container from 'components/Container';
import { mobileOrDesktop } from 'components/ThemeProvider/theme';
import saveAs from 'vendor/FileSaver'

import DateCard from '../MemoPage/DateCard'
import MemoResult from '../MemoPage/MemoResult'

import note from './note.svg';
import remind from './remind.svg';
import bubble from './bubble.svg';
import Layout from '../Layout';
import notice from '../TestPage/notice.svg';
import Sharer from './Sharer'

export const askings = [
  '',
  '',
  '',
  '',
  '',
  '',
  ''
]

const DownloadModule = ({ text, ...props }) => (
  <Flex width={mobileOrDesktop(1, 1/2)} alignItems="center" {...props} borderColor="lightOrange">
    <Box mr="1em">{text}</Box>
    <Box width="2.5em">
      <Circle is={Button.download}>
        <Download width="1.25em" />
      </Circle>
    </Box>
  </Flex>
)

const Notice = ({ isCreating, memo }) => {
  const renderDom = useRef()
  return (
    <Layout>
      <Container py="2em">
        {isCreating && (
          <Box px={mobileOrDesktop('1em', '4em')} bg="titleBgOrange" py="1.5em" borderRadius="1em" mx={mobileOrDesktop('1em', '5em')}>
            <Title
              imgwidth="5em"
              justifyContent={mobileOrDesktop('center', 'auto')}
              flexWrap="wrap"
              width={mobileOrDesktop(1, null)}
              icon={note}
              title=""
              description={'\n'}
            />
          </Box>
        )}
        <Title icon={remind} fontSize="1.5em" title="" justifyContent="center" pt="1.5em" />
        <Flex flexWrap="wrap">
          {askings.map((a, i) => (
            <Box width={mobileOrDesktop(1, 1/2)} key={i}>
              <Flex
                alignItems="center"
                py="1em"
                mx="2em"
                borderBottom="1.5px solid"
                borderColor="titleBgOrange"
                key={i}
              >
                <StyledImage width="2.5em" src={bubble} />
                <Box width={1} mx="1em" fontSize="1.25em">{a}</Box>
              </Flex>
            </Box>
          ))}
        </Flex>
        <Text textAlign="center" mt="1em"> </Text>
        <Box px="4em" bg="titleBgOrange" py="2em" borderRadius="1em" mt="3em">
          <Text textAlign="center" color="titleBlue">
            {isCreating ? '' : ''}
          </Text>
          <Flex mt="1em" flexWrap="wrap">
            <DownloadModule
              text=""
              pr={mobileOrDesktop(0, '2em')}
              justifyContent={mobileOrDesktop('space-between', 'flex-end')}
              borderRight={mobileOrDesktop('none', '1px solid')}
              onClick={() => {
                domtoimage.toBlob(renderDom.current)
                // .then(function (dataUrl) {
                //     var img = new Image();
                //     img.src = dataUrl;
                //     document.body.appendChild(img);
                // })
                  .then((blob) => {
                    saveAs(blob, 'doctor-memo.png');
                  });
              }}
            />
            <DownloadModule
              text="PDF"
              pl={mobileOrDesktop(0, '2em')}
              justifyContent={mobileOrDesktop('space-between', 'unset')}
              borderLeft={mobileOrDesktop('none', '1px solid')}
              borderTop={mobileOrDesktop('1px solid', 'none')}
            />
          </Flex>
        </Box>
        <Title
          flexWrap="wrap"
          width={mobileOrDesktop(1, '6em')}
          icon={notice}
          textAlign={mobileOrDesktop('center', 'left')}
          fontSize="1.25em"
          desfontSize="1em"
          fontWeight="normal"
          title=""
          description=""
          justifyContent="center"
          pt="1.5em"
        />
        <Sharer />
      </Container>
      {createPortal((
        <Box position="absolute" top="-9999px" left="-999px" opacity="0">
          <div ref={renderDom}>
            <Container>
              <DateCard>
                <MemoResult
                  dots={memo.dots}
                  answers={memo.answers}
                />
              </DateCard>
            </Container>
          </div>
        </Box>
      ), document.getElementById('___gatsby'))}
    </Layout>
  );
};

export default Notice;
